
import { defineComponent } from "vue";
// import PostService from "@/PostService.js"

export default defineComponent({
  props: ["projectTimeline","showPtEdit","showPtAdd"],
  data() {
    return { 
      url: "/api/posts",      
      ptItems: {
        _id: "",
        id:0,
        items:[{
          id: 0,          
          day: "",
          keyponit: "",
          output: "",
          qiantoudanwei: "",
          canyurenyuan: ""
        }]
      },
      labels: ['Id','日期','关键点','输出文件','牵头单位','参与人员',],
      items:[{
          id: this.projectTimeline.items.length + 1,          
          day: "",
          keyponit: "",
          output: "",
          qiantoudanwei: "",
          canyurenyuan: ""
      }],
      temp:[{}],
    };
  },  
  async mounted(){
    // var list = await PostService.getPosts(`projectTimeline/${this.$route.params.id}`)
      // console.log(list)
    await fetch(this.url + "/projectTimeline/" + this.$route.params.id ) 
    .then(res => res.json())
    .then(data => {
      this.ptItems = data[0];
      // console.log(this.ptItems)
    })
  },
  methods:{
    async addPt(id: number): Promise<void> {
      this.temp = (this.projectTimeline.items)
      this.temp.push(this.items[0])      
      // console.log(this.temp)
      if(this.items[0].keyponit ==="" || this.items[0].output === "")
      {
        alert("数据不全！");
        return;
      } else{
        try{
          await fetch(this.url + `/projectTimeline/${id}`,{
            method: "PUT",
            headers: {
              "content-type": "application/json"
            },
            body:JSON.stringify({
              items: this.temp
            })
          });
        }catch(err){
          console.log(err)
        }
      }
    },
    async updatePt(id: number): Promise<void> {      
      if(!this.projectTimeline.items)
      {
        alert("数据不全！");
        return;
      } else{
        try{
          await fetch(this.url + `/projectTimeline/${id}`,{
            method: "PUT",
            headers: {
              "content-type": "application/json"
            },
            body:JSON.stringify({
              ...this.projectTimeline,items: this.ptItems.items            
            })
          }) 
          this.$emit('updatePt',this.ptItems) 
        }catch(err){
          console.log(err)
        }
      }
    },
  },
  computed:{
       showEdit(){
         return this.showPtEdit
       },
       showAdd(){
         return this.showPtAdd
       }
    }

});
