
import { defineComponent, onMounted, ref, watch } from "vue";
import Lists from "../models/MonLists.js";
import Terms from "../models/MonTerms.js";

export default defineComponent({
  name: "ToDoLists",
  setup() {
    // var ipAddress: any = ref(localStorage.getItem("ip"))
    var url = ref("api/posts")
    var lists = ref<Lists[]>([]);
    var searchLists = ref<Lists[]>([]);
    var searchValue = ref();
    var timer = ref<any>(0);
    var orderTerm = ref<Terms>("_id");
    const handleTerm = (term: Terms) => {
      orderTerm.value = term;
    };

    onMounted(async (): Promise<void> => {
      let res = await fetch(url.value + "/toDoLists");
      lists.value = await res.json();
      searchLists.value = lists.value;
      // console.log(lists.value[0].type)
    });
    watch(searchValue, (newValue) => {
      clearTimeout(timer.value);
      timer.value = setTimeout(() => {
        if (newValue === "") {
          searchLists.value = lists.value;
        } else {
          searchLists.value = lists.value.filter(
            (list) =>
              list.projectName.includes(newValue) ||
              list.changerA.includes(newValue) ||
              list.type.includes(newValue)
          );
        }
      }, 500);
    });
    watch(searchLists, (newValue) => {
      return newValue;
    });
    watch(orderTerm, (newValue) => {
      if (newValue === "isFav") {
        searchLists.value.sort((a: Lists, b: Lists) => {
          return a[newValue] < b[newValue] ? 1 : -1;
        });
      } else {
        searchLists.value.sort((a: Lists, b: Lists) => {
          return a[newValue] > b[newValue] ? 1 : -1;
        });
      }
    });

    const changeStatus = async (bool: boolean,id: number|string): Promise<void> => {      
      // console.log(state);
      try {
        await fetch(url.value + "/toDoLists/" + id, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            status: !bool
            // ...state, //解析 state  作为 fetch api 的body 发送给服务器 更新数据
          }),
        }).then((res) => {
          if (!res.ok) {
            console.log("Change status failed!");
            return;
          }
        });
      } catch (err) {
        console.log(err);
      }
    };

    const changeIsFav = async (bool: boolean,id: number|string): Promise<void> => {
      try {
        await fetch(url.value + "/toDoLists/" +id, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            isFav: !bool, //解析 state  作为 fetch api 的body 发送给服务器 更新数据
          }),
        }).then((res) => {
          if (!res.ok) {
            console.log("Change isFav failed!");
            return;
          }
        });
        // searchLists.value = {...state }
      } catch (err) {
        console.log(err);
      }
    };

    const deleteTask = async (id: number): Promise<void> => {
      if (confirm("是否确认删除?")) {
        try {
          await fetch(url.value + "/toDoLists/" + id, {
            method: "DELETE",
            headers: {
              "content-type": "application/json",
            },
          });
          searchLists.value = searchLists.value.filter(
            (list) => list._id !== id
          );
        } catch (err) {
          console.log(err);
        }
      }
    };

    return {
      searchValue,
      lists,
      searchLists,
      orderTerm,
      handleTerm,
      changeStatus,
      deleteTask,
      changeIsFav,
    };
  },
});
