
import { defineComponent, onMounted, reactive, ref } from 'vue'

export default defineComponent({
  name: "MachineAccount",
  data(){
    return{
      showZhidu: false,
      showWenjian: false,
      showEdit: false,
    }
  },
  setup(){
    var url = ref('/api/posts');
    var lists: object[] = reactive([{
      _id: "",
      id: 1,
      type: "",
      number: "",
      name: "",
      publishDate: "",
      launchDate: "",
      remark: ""
    }])
    var zhidudata = ref({
      _id: "",
      id: 1,
      type: "制度",
      number: "DEW/S13C",
      name: "",
      publishDate: new Date().toLocaleString(),
      launchDate: new Date().toLocaleString(),
      remark: "",
      jingbanren: "",
      qianfa: ""
    })
    var wenjiandata = ref({
      _id: "",
      id: 1,
      type: "",
      number: "DEW/",
      name: "",
      publishDate: new Date().toLocaleString(),
      launchDate: new Date().toLocaleString(),
      remark: "",
      jingbanren: "",
      qianfa: ""
    })
    var dataedit = ref({
      _id: "",
      id: 1,
      type: "",
      number: "DEW/",
      name: "",
      publishDate: new Date().toLocaleString(),
      launchDate: new Date().toLocaleString(),
      remark: "",
      jingbanren: "",
      qianfa: ""
    })
    var zhiduCount = ref(0)
    var wenjianCount = ref(0)
    onMounted(async ()=>{
      await fetch(url.value + "/zhidutaizhang")
      .then(res => res.json())
      .then(data => {
        lists[0] = data;
        zhiduCount.value = data.length
        zhidudata.value.id = data.length + 1
      })
      await fetch(url.value + "/wenjiantaizhang")
      .then(res => res.json())
      .then(data => {
        lists[1] = data;
        wenjianCount.value = data.length
        wenjiandata.value.id = data.length + 1
      })  
    })
    const addZhidu = async(): Promise<void> =>{
      await fetch(url.value + "/zhidutaizhang",{
        method: "POST",
        headers:{
          "content-type": "application/json"
        },
        body: JSON.stringify({
          ...zhidudata.value
        })
      })
      lists[0] = {...lists[0],"":zhidudata.value} 
      zhiduCount.value += 1;     
    }
    const addWenjian = async(): Promise<void> =>{
      await fetch(url.value + "/wenjiantaizhang",{
        method: "POST",
        headers:{
          "content-type": "application/json"
        },
        body: JSON.stringify({
          ...wenjiandata.value
        })
      })
      lists[1] = {...lists[1],"":wenjiandata.value}
      wenjianCount.value += 1;
    }
    const editData = async (id: number|string,type: string): Promise<void> => { 
      // console.log(id,type)
      // return false
      let temp =''
      if(type === '制度'){
        temp = 'zhidutaizhang'
      } else {
        temp = 'wenjiantaizhang'
      }
      await fetch(url.value + `/${temp}/${id}`)
      .then(res => res.json())
      .then(data => {
        dataedit.value = data
      })
    }
    const UpdateData = async (id: number|string,type: string): Promise<void> => {
      let temp =''      
      if(type === '制度'){
        temp = 'zhidutaizhang'
        
      } else {
        temp = 'wenjiantaizhang'
      }
      await fetch(url.value + `/${temp}/${id}`,{
        method:"PUT",
        headers:{"content-type": "application/json"},
        body: JSON.stringify({
          ...dataedit.value
        })
      })
    
    }
    const selectType = () => {
      /*            <option label="纪要" value="纪要" />
            <option label="传真" value="传真" />
            <option label="联系单" value="联系单" /> 
            会议纪要 M, 传真 F ,工作联系单 C*/
      if(wenjiandata.value.type === '纪要') {
        wenjiandata.value.number = 'DEW/M13C' + new Date().getFullYear()
      } else if (wenjiandata.value.type === '传真'){
        wenjiandata.value.number = 'DEW/F13C' + new Date().getFullYear()
      } else if (wenjiandata.value.type === '联系单'){
        wenjiandata.value.number = 'DEW/C13C' + new Date().getFullYear()
      } else if (wenjiandata.value.type === ''){
        wenjiandata.value.number = 'DEW/'
      }
    }

    return {
      lists,
      zhidudata,
      wenjiandata,
      addZhidu,
      addWenjian,
      zhiduCount,
      wenjianCount,
      editData,
      dataedit,
      UpdateData,
      selectType}
  }

})
