
import { defineComponent } from "vue";
import ProjectOverview from "./ProjectOverview.vue"
import ProjectTimeline from "./ProjectTimeline.vue"

export default defineComponent({
  props: ["propdata"],
  components:{ProjectOverview,ProjectTimeline}, //
  data() {
    return {
      url: "/api/posts",  
      currentId: this.$route.params.id,
      toggleUpdateOverview: false,
      toggleProjectTimeline: false,
      showPtEdit: false,
      showPtAdd: false,
      showOverview: false,
      overview: {
        id: 1,
        contractDay: "",
        contractNo: "",
        contractDuration: "",
        projectPlace: "",
        totalPrice: "",
        constructionContent: "",
        Lessor: "",
        investment: "",
        economicPerformance: "",
        hetongfenjiebiao: "",
        xiangguanfang: "",
        _id: ""
      },
      projectTimeline: {
        _id: "",
        id: 0,
        items:[{
          id: 0,          
          day: "",
          keyponit: "",
          output: "",
          qiantoudanwei: "",
          canyurenyuan: ""
        }]
      },
    };
  },
  async mounted() {
    try {
      await fetch(this.url + "/overview/" + this.$route.params.id)
      .then(res => res.json())
      .then(data => {
        this.overview = data[0];
      })
      await fetch(this.url + "/projectTimeline/" + this.$route.params.id)
      .then(res => res.json())
      .then(data => {
        this.projectTimeline = data[0];
        // console.log(this.projectTimeline)
      })
    } catch (err) {
      console.log(err);
    }
  },
  methods:{
    updatePt(data: any): void{
      this.projectTimeline = data
    },
    async deletePtItem(id: number): Promise<void>{ //async : Promise<void>
      if(confirm("确认删除?")){
        try{
          let newPt = this.projectTimeline.items.filter(item => item.id != id)
          // console.log(newPt)
          await fetch(this.url +"/projectTimeline/"+ this.$route.params.id,{
            method: "PUT",
            headers:{
              "content-type": "application/json"
            },
            body: JSON.stringify({
              ...this.projectTimeline,items : newPt 
            })
          })
          this.projectTimeline.items = newPt
        } catch(err){
          console.log(err)
        }
      }
    },
    updateOv(data: any){
      this.overview = data
    }
  }
});
