
import { defineComponent } from "vue";
import Lists from "@/models/MonLists";
import ProjectRecord from "./ProjectRecord.vue"

export default defineComponent({
  name: "ListDetail",
  components: {ProjectRecord},
  data() {
    return {
      url: "/api/posts",
      ipAddress: localStorage.getItem("ip"),
      propdata:"test prop data",
      list: {
        projectName: "",
        type: "",
        changerA: "",
        changerB: "",
        detail1: "",
        status1: false,
        detail2: "",
        status2: false,
        detail3: "",
        status3: false,
        detail4: "",
        status4: false,
        detail5: "",
        status5: false,
        detail6: "",
        status6: false,
        detail7: "",
        status7: false,
        detail8: "",
        status8: false,
        status: false,
        isFav: false,
        _id: 0,
        id: 0,
      },
    };
  },
  async mounted() {
    try{
      await fetch(`${this.url}/toDoLists/${this.$route.params.id}`) 
      .then((res) => res.json())
      .then((data) => {
        this.list = data[0];
        // console.log(this.list)
      });
    } catch(err) {
      console.log(err)
    }
  },
  methods: {
    async changeStatus(list: Lists, order: number) {
      try{        
        // let  state!: Lists;
        let  state!: object;
        // switch (order) {
        //   case 1:
        //     state = { ...list, status1: !list.status1 };
        //     break;
        //   case 2:
        //     state = { ...list, status2: !list.status2 };
        //     break;
        //   case 3:
        //     state = { ...list, status3: !list.status3 };
        //     break;
        //   case 4:
        //     state = { ...list, status4: !list.status4 };
        //     break;
        //   case 5:
        //     state = { ...list, status5: !list.status5 };
        //     break;
        //   case 6:
        //     state = { ...list, status6: !list.status6 };
        //     break;
        //   case 7:
        //     state = { ...list, status7: !list.status7 };
        //     break;
        //   case 8:
        //     state = { ...list, status8: !list.status8 };
        //     break;
        // }
        switch (order) {
          case 1:
            state = {status1: !list.status1};
            break;
          case 2:
            state = {status2: !list.status2};   
            break;
          case 3:
            state = {status3: !list.status3};  
            break;
          case 4:
            state = {status4: !list.status4};
            break;
          case 5:
            state = {status5: !list.status5};
            break;
          case 6:
            state = {status6: !list.status6};
            break;
          case 7:
            state = {status7: !list.status7};
            break;
          case 8:
            state = {status8: !list.status8};
            break;
        }
        await fetch(`${this.url}/todolists/` + list._id, {
          method: "PUT",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify({
             ...state
          }),
        })
      } catch(err){
        console.log(err)
      }
    }
  },

});
