
import { defineComponent, reactive, ref } from 'vue'
export default defineComponent({
  name: "AddList",
  setup(){
    const url = ref<string>('api/posts')
    const lists = reactive({        
        projectName: "",
        type: "",      
        changerA: "",
        changerB: "",
        detail1: "前期准备",
        status1: false,
        detail2: "初步设计",
        status2: false,
        detail3: "招标",
        status3: false,
        detail4: "施工",
        status4: false,
        detail5: "竣工验收",
        status5: false,
        detail6: "竣工结算",
        status6: false,
        detail7: "项目运营",
        status7: false,
        detail8: "备注",
        status8: false,
        status: false,
        isFav: false,
    });
    const overView = reactive({
        contractDay: new Date().toLocaleString(),
        contractNo: "DEW***",
        contractDuration: 360,
        projectPlace: "**",
        totalPrice: 125000,
        constructionContent: "**",
        Lessor: "龙马重工",
        investment: 100000000,
        economicPerformance: 25125851,
        hetongfenjiebiao: "",
        xiangguanfang: ""
    })
    const projectTimeline = reactive({      
      items: [
        {          
          day: "2022-03-21",
          keyponit: "发送电子技术要求",
          output: "技术文件",
          qiantoudanwei: "工程建设部",
          canyurenyuan: "刘源、张先林等"
        }
      ]
    })

    const addList = async (): Promise<void> => {
      if(lists.projectName.length ===0 || lists.type.length ===0) 
      { alert("要素不全！")
        return}
      try{
        await fetch(url.value + '/todolists',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...lists})
        })
        await fetch(url.value + '/overview',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...overView})
        })
        await fetch(url.value + '/projectTimeline',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...projectTimeline})
        })
      } catch(err){
        console.log(err)
      }
    }
    return {
      lists,
      addList,
      overView,
      projectTimeline,
    }
  },
})
